<script>
import { ref, onMounted, onBeforeMount, watch } from "vue"
import { useRouter } from "vue-router"
import { Toast } from "../../components/global/swal"
import Header from "../../components/global/header/HeaderArea2.vue"
import CitySelecter from "../../components/global/CitySelecter.vue"
import EatType from "../../components/global/EatType.vue"
import ActType from "../../components/global/ActType.vue"
import DiseaseType from "../../components/global/DiseaseType.vue"
import {
  apiGetCareRecipientInfo,
  apiPutCareRecipientInfo,
  apiHospital,
  apiCaringOrg,
} from "@/api/api"
import { getUrlId } from "@/utils/handle"
import { focusInput } from "@/utils/fixFooter"

export default {
  setup() {
      
    const urlId = getUrlId().urlId
    const urlLink = `/careRecipient/info/${urlId}`
    const router = useRouter()
    let data = ref({})
    let hospitalData = ref([])
    let sanatoriumData = ref([])
    data.value.disease = []

    // 控制頁面
    let stepStyle = ref(["nav-link active", "nav-link", "nav-link"])
    let showPage = ref("page1")
    let title = ref("基本資料")

    onMounted(() => {
      focusInput()
    })
    onBeforeMount(async () => {
      try {
        // call api
        data.value = await apiGetCareRecipientInfo({ id: urlId })
        data.value = data.value.data
        //get 醫院或療養院清單
        if (data.value.servicePlace.placeType.id == 2) {
          hospitalData.value = await apiHospital({
            cityId: data.value.servicePlace.city.id,
            areaId: data.value.servicePlace.area.id,
          })
          hospitalData.value = hospitalData.value.data
        } else if (data.value.servicePlace.placeType.id == 3) {
          sanatoriumData.value = await apiCaringOrg({
            cityId: data.value.servicePlace.city.id.value,
            areaId: data.value.servicePlace.area.id,
          })
          sanatoriumData.value = sanatoriumData.value.data
        }
      } catch (error) {
        console.log(error)
      }
    })

    //page1
    //取飲食類型
    // const updateEatType = (val) => {
    //   data.value.eatType = val.map(item => parseInt(item))
    //   // console.log(data.eatType)
    // }
    // const updateEatInput = (val) => {
    //   data.value.eatOther = val
    //   // console.log(data.eatOther)
    // }
    //取行動能力
    const updateActType = (val) => {
      save
      data.value.abilityToAct = val.map((item) => parseInt(item))
      // console.log(data.value.abilityToAct)
    }
    const updateActInput = (val) => {
      data.value.abilityToActOther = val
      // console.log(data.abilityToActOther)
    }
    //檢查表單|下一頁
    const form1 = ref(null)
    const save = (e) => {
      e.preventDefault()

      if (form1.value.reportValidity()) {
        // console.log(data)
        if (data.value.abilityToAct.length <= 0) {
          Toast("請填寫行動能力!")
          return
        }
        showPage.value = "page2"
        stepStyle.value = ["nav-link visited", "nav-link active", "nav-link"]
        title.value = "照顧地點"
      }
    }

    //page2
    const cityIdx = ref(0)
    const areaIdx = ref(0)

    //取縣市鄉鎮代碼
    const updateCityIdx = (val) => {
      data.value.servicePlace.city.id = parseInt(val.value)
      cityIdx.value = val.value
    }
    const updateAreaIdx = (val) => {
      data.value.servicePlace.area.id = parseInt(val.value)
      areaIdx.value = val.value
    }
    // 切換地區，載入醫院、療養院
    watch(
      () => areaIdx.value,
      async () => {
        if (data.value.servicePlace.placeType.id == 2) {
          hospitalData.value = await apiHospital({
            cityId: cityIdx.value,
            areaId: areaIdx.value,
          })
          if (hospitalData.value.data.length == 0) {
            Toast("該區域無資料")
            hospitalData.value = []
          } else {
            hospitalData.value = hospitalData.value.data
            // console.log(hospitalData.value)
          }
        }
        if (data.value.servicePlace.placeType.id == 3) {
          sanatoriumData.value = await apiCaringOrg({
            cityId: cityIdx.value,
            areaId: areaIdx.value,
          })
          if (sanatoriumData.value.data.length == 0) {
            Toast("該區域無資料")
            sanatoriumData.value = []
          } else {
            sanatoriumData.value = sanatoriumData.value.data
            // console.log(sanatoriumData.value)
          }
        }
      }
    )
    //切換居住類別，載入醫院、療養院
    watch(
      () => data.value.servicePlace?.placeType?.id,
      async (newVal, oldVal) => {
        if (oldVal) {
          if (data.value.servicePlace.placeType.id == 1) {
            data.value.servicePlace.place.address = ""
          }
          if (data.value.servicePlace.placeType.id == 2) {
            cityIdx.value = data.value.servicePlace.city.id
            areaIdx.value = data.value.servicePlace.area.id
            hospitalData.value = await apiHospital({
              cityId: cityIdx.value,
              areaId: areaIdx.value,
            })
            if (hospitalData.value.data.length == 0) {
              Toast("該區域無資料")
              hospitalData.value = []
            } else {
              hospitalData.value = hospitalData.value.data
              // console.log(hospitalData.value)
            }
          }
          if (data.value.servicePlace.placeType.id == 3) {
            cityIdx.value = data.value.servicePlace.city.id
            areaIdx.value = data.value.servicePlace.area.id
            sanatoriumData.value = await apiCaringOrg({
              cityId: cityIdx.value,
              areaId: areaIdx.value,
            })
            if (sanatoriumData.value.data.length == 0) {
              Toast("該區域無資料")
              sanatoriumData.value = []
            } else {
              sanatoriumData.value = sanatoriumData.value.data
              // console.log(sanatoriumData.value)
            }
          }
        }
      }
    )
    //檢查表單|下一頁
    const form2 = ref(null)
    const save2 = (e) => {
      e.preventDefault()
      if (form2.value.reportValidity()) {
        // console.log(data)
        showPage.value = "page3"
        stepStyle.value = [
          "nav-link visited",
          "nav-link visited",
          "nav-link active",
        ]
        title.value = "病症(複選)"
      }
    }

    //page3
    //取病症代碼
    const updateDiseaseData = (val) => {
      data.value.checkList = val
      // console.log(data.value.checkList)
    }
    //檢查表單|送出資料
    const save3 = async () => {
      data.value.servicePlace.city.id = cityIdx.value
      data.value.servicePlace.area.id = areaIdx.value
      //處理putData
      if (data.value?.abilityToAct[0]?.id) {
        data.value.abilityToAct = data.value.abilityToAct.map((item) =>
          parseInt(item.id)
        )
      }
      // if (data.value.eatType[0].id) {
      //   data.value.eatType = data.value.eatType.map(item => parseInt(item.id))
      // }
      // console.log(data.value)
      let msg = await apiPutCareRecipientInfo({ id: urlId }, data.value)
      if (msg.data == "更新成功") {
        Toast("修改成功")
      }
      router.push({ path: "/careRecipient/list" })
    }

    //回上一頁
    const backToPage1 = () => {
      showPage.value = "page1"
      stepStyle.value = ["nav-link active ", "nav-link", "nav-link"]
      title.value = "基本資料"
    }
    const backToPage2 = () => {
      showPage.value = "page2"
      stepStyle.value = ["nav-link visited ", "nav-link active", "nav-link"]
      title.value = "照顧地點"
    }

    return {
      stepStyle,
      showPage,
      title,
      save,
      save2,
      save3,
      data,
      backToPage1,
      backToPage2,
      form1,
      form2,
      updateCityIdx,
      updateAreaIdx,
      hospitalData,
      sanatoriumData,
      updateActType,
      updateActInput,
      updateDiseaseData,
      urlLink,
    }
  },
  components: { Header, CitySelecter, ActType, DiseaseType },
}
</script>

<template>
  <Header v-if="showPage == 'page1'" :path="urlLink" />
  <Header v-if="showPage == 'page2'" :clickFun="backToPage1" />
  <Header v-if="showPage == 'page3'" :clickFun="backToPage2" />
  <div class="careRecipientAdd">
    <div class="mb-3 step-container">
      <ul class="nav justify-content-center">
        <!-- 已完成 visited，目前 active -->
        <li class="nav-item">
          <a href="javascript:void(0);" :class="stepStyle[0]"><span>1</span></a>
        </li>
        <li class="nav-item">
          <a href="javascript:void(0);" :class="stepStyle[1]"><span>2</span></a>
        </li>
        <li class="nav-item">
          <a href="javascript:void(0);" :class="stepStyle[2]"><span>3</span></a>
        </li>
      </ul>
    </div>
    <div class="title">
      <p>{{ title }}</p>
    </div>
    <hr />
    <div class="main" v-show="showPage == 'page1'">
      <form ref="form1">
        <div class="mb-3">
          <label class="form-label">名稱<span class="star">*</span></label>
          <input
            type="text"
            class="form-control"
            placeholder="請輸入名稱"
            v-model="data.name"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label">性別<span class="star">*</span></label>
          <div class="input-group">
            <div class="col form-check">
              <input
                type="radio"
                class="form-check-input"
                value="Male"
                id="Male"
                v-model="data.gender"
              />
              <label class="form-check-label" for="Male">男性</label>
            </div>
            <div class="col form-check">
              <input
                type="radio"
                class="form-check-input"
                value="Female"
                id="Female"
                v-model="data.gender"
              />
              <label class="form-check-label" for="Female">女性</label>
            </div>
          </div>
        </div>
        <div class="mb-3">
          <label class="form-label">年齡/歲<span class="star">*</span></label>
          <input
            type="number"
            class="form-control"
            placeholder="請輸入年齡"
            v-model="data.age"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label">身高/公分<span class="star">*</span></label>
          <input
            type="number"
            class="form-control"
            placeholder="請輸入身高/公分"
            v-model="data.height"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label">體重/公斤<span class="star">*</span></label>
          <input
            type="number"
            class="form-control"
            placeholder="請輸入體重/公斤"
            v-model="data.weight"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label"
            >與申請人關係<span class="star">*</span></label
          >
          <input
            type="text"
            class="form-control"
            placeholder="請輸入與申請人關係"
            v-model="data.relationship"
            required
          />
        </div>
        <!-- <div class="mb-3">
          <label class="form-label">是否有傳染性<span class="star">*</span></label>
          <div class="input-group">
            <div class="col form-check">
              <input type="radio" class="form-check-input" value="1" id="yes" v-model="data.isContagious">
              <label class="form-check-label" for="yes">是</label>
            </div>
            <div class="col form-check">
              <input type="radio" class="form-check-input" value="0" id="no" v-model="data.isContagious">
              <label class="form-check-label" for="no">否</label>
            </div>
          </div>
        </div> -->
        <!-- <EatType @eat-type="updateEatType" @eat-input="updateEatInput" :eatType_p="data.eatType"
          :otherEat_p="data.otherEatType" v-if="data.eatType" /> -->
        <ActType
          @act-type="updateActType"
          @act-input="updateActInput"
          :actType_p="data.abilityToAct"
          :otherAct_p="data.otherAbilityToAct"
          v-if="data.abilityToAct"
        />
      </form>
    </div>
    <div class="main2" v-show="showPage == 'page2'" v-if="data.servicePlace">
      <form ref="form2">
        <div class="mb-3">
          <label class="form-label">居住方式<span class="star">*</span></label>
          <div class="input-list">
            <div class="form-check-inline">
              <input
                type="radio"
                class="form-check-input"
                value="1"
                id="live1"
                v-model="data.servicePlace.placeType.id"
              />
              <label class="form-check-label" for="live1">居家</label>
            </div>
            <div class="form-check-inline">
              <input
                type="radio"
                class="form-check-input"
                value="2"
                id="live2"
                v-model="data.servicePlace.placeType.id"
              />
              <label class="form-check-label" for="live2">醫院</label>
            </div>
            <div class="form-check-inline">
              <input
                type="radio"
                class="form-check-input"
                value="3"
                id="live3"
                v-model="data.servicePlace.placeType.id"
              />
              <label class="form-check-label" for="live3">療養院</label>
            </div>
          </div>
          <CitySelecter
            @city-idx="updateCityIdx"
            @area-idx="updateAreaIdx"
            :cityIdx_p="data.servicePlace.city.id"
            :areaIdx_p="data.servicePlace.area.id"
          />
        </div>
        <div class="mb-3">
          <div v-if="data.servicePlace.placeType.id == 1">
            <input
              type="text"
              class="form-control mt-1"
              placeholder="請輸入居家地址"
              v-model="data.servicePlace.place.address"
              required
            />
            <div class="note">
              提醒您，居家地址必須填寫正確門牌位置，以方便管理人員打卡
            </div>
          </div>
          <div v-if="data.servicePlace.placeType.id == 2">
            <div class="mb-3">
              <select
                class="form-control form-select"
                v-model="data.servicePlace.place.id"
                required
              >
                <option value="undefined" disabled>請選擇醫院</option>
                <option
                  v-for="item in hospitalData"
                  :value="item.properties.id"
                  :key="item.properties.id"
                >
                  {{ item.properties.name }}
                </option>
              </select>
            </div>
          </div>
          <div v-if="data.servicePlace.placeType.id == 3">
            <div class="mb-3">
              <select
                class="form-control form-select"
                v-model="data.servicePlace.place.id"
                required
              >
                <option value="undefined" disabled>請選擇療養院</option>
                <option
                  v-for="item in sanatoriumData"
                  :value="item.properties.id"
                  :key="item.properties.id"
                >
                  {{ item.properties.name }}
                </option>
              </select>
            </div>
          </div>
        </div>
      </form>
    </div>
    <div class="main3" v-show="showPage == 'page3'" v-if="data.checkList">
      <DiseaseType
        @disease-data="updateDiseaseData"
        :disease_p="data.checkList"
      />
    </div>
  </div>
  <footer class="c-footer">
    <div class="ui-tabbtn">
      <div class="row">
        <div class="col">
          <button
            class="btn btn-primary"
            type="button"
            @click="save"
            v-if="showPage == 'page1'"
          >
            確認並繼續
          </button>
          <button
            class="btn btn-primary"
            type="button"
            @click="save2"
            v-if="showPage == 'page2'"
          >
            確認並繼續
          </button>
          <button
            class="btn btn-primary"
            type="button"
            @click="save3"
            v-if="showPage == 'page3'"
          >
            完成新增
          </button>
        </div>
      </div>
    </div>
  </footer>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";
@import "@/assets/custom/mixins";

Header {
  background-color: #ffffff;
}

.careRecipientAdd {
  margin-bottom: calc($footer-height + 15px);
  margin-top: $header-height;
  padding: 1.25rem;
}

.title {
  font-size: 1.25rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
}
</style>
